<template>
    <div>
        <div data-v-152ab910="" data-v-ade07ba4="" class="sub-header">
            <div data-v-152ab910="" class="back" @click="$router.go(-1)"></div>
            <div data-v-152ab910="" class="title">{{orderTitle}}</div><!----></div>
            <orderTags :status="status"/>
        <div class="main">
            <router-view/>
        </div>
    </div>
</template>

<script>
    import orderTags from  "../../../components/order_tags"
    export default {
        data(){
            return {
                status:this.$route.query.status?this.$route.query.status:"all",
                orderTitle:"全部订单"
            }
        },
        components:{
            orderTags
        },
        created(){
            //安全认证
            this.$utils.safeUser(this);
            this.goTitle()
        },
        mounted(){
            document.title=this.$route.meta.title;
        },
        methods:{
            //动态变化网页title
            goTitle(){
                switch (this.status) {
                    case "all":
                        this.orderTitle = "全部订单";
                        document.title = this.orderTitle;
                        break;
                    case "0":
                        this.orderTitle = "待付款";
                        document.title = this.orderTitle;
                        break;
                    case "1":
                        this.orderTitle = "待收货";
                        document.title = this.orderTitle;
                        break;
                    case "2":
                        this.orderTitle = "已收货-待评价";
                        document.title = this.orderTitle;
                        break;
                    default:
                        this.orderTitle = "全部订单";
                        document.title = this.orderTitle;
                        break;
                }
            }
        },
        beforeRouteUpdate(to,from,next){
            this.status = to.query.status;
            this.goTitle();
            next()
        }
    }
</script>

